
 body{
  color: #334D6E;
  font-family: 'Poppins', sans-serif;
  height: auto;
  background-color: #b8bdbdc4;

}
.main_About{
  width: 100%;
  display: flex;
  position: relative;
  
}
.logobox_About{
  
    width: 17%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    box-shadow: 0 0 4px;
  }

.logo{
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
}
.user_About{
  margin: 15px 1px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.user_About h3,.user_About p{
  margin: 0;
}

.NavContainer{
width: 83%;
height: auto;
position: absolute;
left: 17%;
}
.navbar_About{
height: 50px;
width: 82.7%;
left: 17.2%;
display: flex;
box-shadow: 0 1px 4px grey;
background-color: white;
position: fixed;
top: 0;
z-index: 1;
align-items: center;
justify-content: flex-end;

}
.list{
  display: flex;
}
.list li {
  margin:0 50px 0 10px;
    list-style: none;
  
   
  }
  .list li a{
    text-decoration: none;
    color: #192A3E;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .list li a .fas{
  padding-right: 5px;
  }
  .whole{
    position: absolute;
    top: 58px;
    width: 100%;
  }
  .About_btns{
    
      margin: 10px 26px;
      display: flex;
      align-items: center;
      
      }
      .btns p{
        padding-left:20px ;
      }
      .About_jobbtn{
        padding: 6px;
        border: none;
        font-family: 'Poppins', sans-serif;
        font-size: 12px;
        background-color: #e5eff5;
        cursor: pointer;
        border-radius: 4px;
        color: #192A3E;
      }
      .About_jobbtn{
        margin-left: 5px;
        width: 85px;
        cursor: pointer;
        }
.AbtCmpny{
  width: 97%;
  height: auto;
  background-color: #fff;
  margin:20px auto;
}

.AbtCmpny h2{
  padding: 10px 20px;
  text-align: center;
  border-bottom: .1px solid rgba(128, 128, 128, 0.623);
}
.AbtCmpny p{
  padding: 20px 0 10px 20px;
  width: 640px;
  margin: auto;
  text-align: center;
}
.Abtform{
  padding: 0 0 10px 20px;
}
.CrtNjob{
  margin-left: 20px;
  font-size: 16px;
  color: #334D6E;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
.Abtform input{
padding: 6px;
width: 300px;
border-radius: 4px;
border:1px solid grey;
outline: none;
font-size: 14px;
display: flex;
align-items: center;
}
.Abtform div{
  padding: 5px;
}
.Clctn input{
  margin-bottom: 10px;
}

.Abtfrm{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.NEXTBTN{
  display: flex;
  justify-content: center;
}
.nxtstpbtn{
width: 120px;
height: 35px;
background-color: #06be37;
color: white;
border: none;
border-radius: 4px;


cursor: pointer;
}
.nxtstpbtn:hover{
  color: #06be37;
  background-color: #fff;
  border: 1px solid #06be37;
  border-radius: 4px;
}
.fa-sign-out-alt:hover, .Logout a:hover{
  color: rgba(255, 0, 0, 0.87);
}

.list li a .fas, .list li a .far{
  padding-right: 5px;
}
.About_Emailbtn{
  width: 160px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008BDF;
  color: #fff;
  border: none;
  margin: 10px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}



.About_Supportbtn{
  width: 160px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192A3E;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 10px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;}

.AbtComp_leftbtns{
  margin: 50px 0 70px 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}
.lftmail .fa-envelope, .lftphone .fa-phone-alt{
  padding-right: 8px;
  color: #192A3E;
}
.lftmail a{
  color: #008BDF;
  text-decoration: none;
}
.lftmail, .lftphone{
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone{
  color: #192A3E;
  text-align: center;
  cursor: pointer;
}


.Emailbtn:hover{
  color:#008BDF;
  background-color: #fff;
  border: 1px solid #008BDF;
}
.Supportbtn:hover{
  color: #c4dceb;
  background-color: #192A3E;
  border: 1px soli #c4dceb;
} 

 
