  }
  .logo img{
    height: 13px;
    width: 54px;
  }
  .user_About{
    margin: 6px 0;
  }
  .user_About h3,.user_About p{
    margin: 2px 0;
    font-size: 5.5px;
  }
  .user_About p{
    padding: 0;
    font-size: 4.3px;
  }
  .list li a .fas, .list li a .far{
    padding-right: 2px;
