    padding-right: 2px;
  }
  .lftmail{
    display: block;
    text-align:center;
    font-size: 5.5px;
  }
  .lftphone{
    font-size: 5.5px;
  }


  .navbar_About{
    height: 25px;
    width: 82.7%;
    left: 17.2%;
    }
    .list li {
      margin:0 15px 0 3px;
      }
      .list li a .fas{
      padding-right: 2px;
      }
      .whole{
        top: 30px;
      }
      .About_btns{
        
          margin: 5px 9px;
          
          }
          .btns p{
            padding-left:10px ;
          }
          .About_jobbtn{
            padding: 3px;
            font-size: 6px;
            margin-left: 3px;
            width: 40px;
            border-radius: 2px;
          }
          .AbtCmpny{
           margin:6px auto;
          }
    
          .AbtCmpny h2{
            padding: 5px 10px;
         }
          .Abtform{
            padding: 0 0 5px 10px;
        }
        .CrtNjob{
          margin-left: 10px;
          font-size: 6px;
        }
        .Abtform input{
          padding: 3px;
          width: 90%;
          border-radius: 3px;
          border:1px solid grey;
          outline: none;
          font-size: 6px;
          
          }
          .Abtform div{
            padding: 3px;
          }
          .Clctn input{
            margin-bottom: 5px;
          }
          
          .Abtfrm{
            margin-right: 5px;
            display: block;
          }
          .AbtCmpny p{
            width: auto;
            padding: 0 10px;
          }
          .nxtstpbtn{
            width: 50px;
            height: 20px;
            font-size: 7px;
            }
}



 
